import React from "react"
import { useState } from "react"

const SelectForm = () => {
  const [selectIsValid, setSelectIsValid] = useState(false)
  const handleValid = (e) => {
    if (e.target.value !== "") {
      setSelectIsValid(true)
    } else {
      setSelectIsValid(false)
    }
  }
  return (
    <div
      className="cta__opened__welcome__with-selection"
      style={{ background: selectIsValid && "#FFF0C0" }}
    >
      <select
        className="decorated"
        name="user_request"
        required
        onInput={handleValid}
      >
        <option className="option" value="">
          Votre demande
        </option>
        <option className="option" value="Court-metrage">
          Court-metrage
        </option>
        <option value="Documentaire">Documentaire</option>
        <option value="Clip">Clip</option>
        <option value="Communicationt">Communication</option>
      </select>
      <i className="fa-solid fa-clipboard-check"></i>
    </div>
  )
}

export default SelectForm
