import { useState } from "react"
import { NavLink, useLocation } from "react-router-dom"

import SubNavContainer from "./SubNavContainer"

const Navbar = ({ doc, court, clips, com }) => {
  const [animationIsStart, setAnimationIsStart] = useState(true)

  const location = useLocation()

  let courtReverse = false
  let docReverse = false
  let clipsReverse = false
  let comReverse = false

  switch (location.state) {
    case "court-metrages":
      courtReverse = true
      break
    case "documentaires":
      docReverse = true
      break
    case "clips":
      clipsReverse = true
      break
    case "communication":
      comReverse = true
      break

    default:
      break
  }
  const handleRemoveLi = () => {
    if (location.state === null) {
      setAnimationIsStart(false)
    }
  }
console.log(location.state)
  return (
    <nav className={"navbar"}>
      <div
        className={
          location.state === true
            ? "navbar__wrapper active-anim"
            : "navbar__wrapper"
        }
      >
        {animationIsStart ? (
          <ul className="navbar__wrapper__animations">
            <li
              className={
                doc
                  ? "active-court"
                  : clips
                  ? "active-clips"
                  : com
                  ? "active-coms"
                  : docReverse
                  ? "active-court-reverse "
                  : clipsReverse
                  ? "active-clips-reverse "
                  : comReverse
                  ? "active-coms-reverse"
                  : undefined
              }
            >
              <NavLink to="/court-metrages" id="court-metrages">
                Court-métrages
              </NavLink>
            </li>
            <li
              className={
                doc
                  ? "active-doc"
                  : court
                  ? "active-courts"
                  : clips
                  ? "active-clips"
                  : com
                  ? "active-coms"
                  : courtReverse
                  ? "active-courts-reverse "
                  : docReverse
                  ? "active-doc-reverse "
                  : clipsReverse
                  ? "active-clips-reverse "
                  : comReverse
                  ? "active-coms-reverse"
                  : undefined
              }
            >
              <NavLink to="/documentaires" id="documentaires">
                Documentaires
              </NavLink>
            </li>
            <li
              onAnimationEnd={handleRemoveLi}
              className={
                clips
                  ? "active-clip"
                  : court
                  ? "active-courts"
                  : doc
                  ? "active-docs"
                  : com
                  ? "active-coms"
                  : courtReverse
                  ? "active-courts-reverse "
                  : docReverse
                  ? "active-docs-reverse "
                  : clipsReverse
                  ? "active-clip-reverse "
                  : comReverse
                  ? "active-coms-reverse"
                  : undefined
              }
            >
              <NavLink to="/clips" id="clips">
                Clips Musicaux
              </NavLink>
            </li>
            <li
              onAnimationEnd={handleRemoveLi}
              className={
                com
                  ? "active-com"
                  : court
                  ? "active-courts"
                  : doc
                  ? "active-docs"
                  : clips
                  ? "active-com-down"
                  : courtReverse
                  ? "active-courts-reverse"
                  : docReverse
                  ? "active-docs-reverse"
                  : clipsReverse
                  ? "active-com-down-reverse"
                  : comReverse
                  ? "active-com-reverse"
                  : undefined
              }
            >
              <NavLink to="/communication" id="communication">
                Com. Int/Ext
              </NavLink>
            </li>
          </ul>
        ) : (
          //-------Show page contained -------
          <SubNavContainer doc={doc} court={court} clips={clips} com={com} />
        )}
      </div>
    </nav>
  )
}

export default Navbar
