import React, { useRef } from "react"
import emailjs from "@emailjs/browser"
import SelectForm from "./SelectForm"

const Form = () => {
  const form = useRef()

  const handleSendEmail = (e) => {
    e.preventDefault()
    alert("Merci pour votre message")
    const serviceId = "service_0wt6fdx"
    const templateId = "template_fcicl9g"

    document.forms[0].message.value = document.forms[0].message.value
      .replaceAll("&", "")
      .replaceAll("<", "")
      .replaceAll(">", "")
      .replaceAll('"', "")
      .replaceAll("'", "")
      .replaceAll("\r", "<br />")
      .replaceAll("\n", "<br />")

    console.log(document.forms[0].message.value)

    emailjs
      .sendForm(
        serviceId,
        templateId,
        form.current,
        process.env.REACT_APP_API_PUBLIC_KEY
      )
      .then((result) => {
        console.log(result.text)
        e.target.reset()
      })
      .catch((error) => {
        console.log(error)
      })
  }
  return (
    <form ref={form} onSubmit={handleSendEmail}>
      <div className="cta__opened__welcome__field">
        <label htmlFor="mail">E-mail</label>
        <input
          type="email"
          id="mail"
          name="user_email"
          placeholder="Email"
          required
        />
        <i className="fa-solid fa-envelope"></i>
      </div>
     <SelectForm />
      <div className="cta__opened__welcome__field__message">
        <label>Message</label>
        <textarea
          name="message"
          placeholder="Votre message"
          required
        ></textarea>
      </div>
      <div className="cta__opened__welcome__submit">
        <button type="submit" name="Envoyer">
          Envoyer
        </button>
      </div>
    </form>
  )
}

export default Form
