import React from "react"
import { BrowserRouter, Routes, Route, Navigate } from "react-router-dom"
import CourtMetrages from "../../pages/CourtMetrages"
import Documentaires from "../../pages/Documentaires"
import Clips from "../../pages/Clips"
import Communication from "../../pages/Communication"
import SubHome from "../../pages/SubHome"
import Home from "../../pages/Home"


const IndexRoutes = () => {
  
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/sub-home" element={<SubHome />} />
        <Route path="/court-metrages" element={<CourtMetrages />} />
        <Route path="/documentaires" element={<Documentaires />} />
        <Route path="/clips" element={<Clips />} />
        <Route path="/communication" element={<Communication />} />
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
    </BrowserRouter>
  )
}

export default IndexRoutes
